/**
 * User State Graph Types
 * 
 * Core type definitions for the graph-based user state management system.
 * Every user interaction, page visit, and data point becomes a node in this graph.
 */

// ============================================================================
// Core Graph Types
// ============================================================================

/**
 * Unique identifier for graph nodes and edges
 */
export type NodeId = string;
export type EdgeId = string;

/**
 * Timestamp in milliseconds since epoch
 */
export type Timestamp = number;

/**
 * Node types represent different kinds of entities in the user's browsing experience
 */
export enum NodeType {
  // Web browsing
  PAGE = 'page',                    // Web page visit
  DOMAIN = 'domain',                // Website domain
  URL_PATTERN = 'url_pattern',      // URL pattern or route
  
  // Content and data
  CONTENT = 'content',              // Page content or text snippet
  IMAGE = 'image',                  // Image or media
  LINK = 'link',                    // Hyperlink
  FORM = 'form',                    // Form interaction
  
  // User actions
  ACTION = 'action',                // User action (click, scroll, etc.)
  SEARCH = 'search',                // Search query
  INPUT = 'input',                  // User input or data entry
  
  // Entities and objects
  PERSON = 'person',                // Person or user
  ORGANIZATION = 'organization',     // Company or organization
  PRODUCT = 'product',              // Product or service
  DOCUMENT = 'document',            // Document or file
  
  // Tools and applications
  TOOL = 'tool',                    // External tool or application
  WORKFLOW = 'workflow',            // Workflow or process
  PLAYBOOK = 'playbook',            // Automation playbook
  
  // Meta and system
  SESSION = 'session',              // Browsing session
  CONTEXT = 'context',              // Context or situation
  INTENT = 'intent',                // User intent or goal
  TAG = 'tag',                      // Classification tag
  NOTE = 'note',                    // User annotation or note
}

/**
 * Edge types represent relationships between nodes
 */
export enum EdgeType {
  // Navigation relationships
  NAVIGATED_TO = 'navigated_to',        // User navigated from A to B
  CONTAINS = 'contains',                // A contains B (page contains link)
  REFERENCES = 'references',            // A references B (page references person)
  
  // Temporal relationships
  FOLLOWED_BY = 'followed_by',          // A was followed by B in time
  TRIGGERED_BY = 'triggered_by',        // A was triggered by B
  
  // Semantic relationships
  RELATED_TO = 'related_to',            // A is related to B
  SIMILAR_TO = 'similar_to',            // A is similar to B
  PART_OF = 'part_of',                  // A is part of B
  INSTANCE_OF = 'instance_of',          // A is an instance of B
  
  // Action relationships
  PERFORMED_ON = 'performed_on',        // Action performed on target
  USED_WITH = 'used_with',              // Tool used with content
  CREATED = 'created',                  // A created B
  MODIFIED = 'modified',                // A modified B
  
  // User intent relationships
  INTERESTED_IN = 'interested_in',      // User interested in topic
  WORKING_ON = 'working_on',            // User working on project
  SEARCHING_FOR = 'searching_for',      // User searching for information
  
  // Tool and workflow relationships
  AUTOMATED_BY = 'automated_by',        // A is automated by tool B
  ENHANCED_BY = 'enhanced_by',          // A is enhanced by tool B
  DEPENDS_ON = 'depends_on',            // A depends on B
}

/**
 * Data source indicates how the node/edge was created
 */
export enum DataSource {
  SCRAPING = 'scraping',           // Automatically scraped from page
  SDK = 'sdk',                     // Provided by page SDK
  USER_INPUT = 'user_input',       // Explicitly entered by user
  TOOL_UPDATE = 'tool_update',     // Updated by external tool
  INFERENCE = 'inference',         // Inferred from other data
  IMPORT = 'import',               // Imported from external source
  API = 'api',                     // Retrieved from API
}

/**
 * Confidence level for inferred or uncertain data
 */
export enum ConfidenceLevel {
  CERTAIN = 1.0,      // Explicitly confirmed
  HIGH = 0.8,         // Very likely correct
  MEDIUM = 0.6,       // Probably correct
  LOW = 0.4,          // Possibly correct
  UNCERTAIN = 0.2,    // Unlikely but possible
}

// ============================================================================
// Node Definition
// ============================================================================

/**
 * Core properties common to all nodes
 */
export interface BaseNodeData {
  // Identity
  title?: string;                   // Human-readable title
  description?: string;             // Longer description
  url?: string;                     // Associated URL (if applicable)
  
  // Content
  content?: string;                 // Text content or data
  metadata?: Record<string, any>;   // Flexible metadata storage
  
  // Classification
  tags?: string[];                  // User-defined tags
  categories?: string[];            // System categories
  
  // Quality and trust
  confidence?: ConfidenceLevel;     // Confidence in data accuracy
  verified?: boolean;               // Whether data has been verified
  
  // External references
  externalId?: string;              // ID in external system
  externalSource?: string;          // Name of external source
}

/**
 * Node-type specific data interfaces
 */
export interface PageNodeData extends BaseNodeData {
  url: string;                      // Required for pages
  title?: string;                   // Page title
  favicon?: string;                 // Favicon URL
  openGraph?: Record<string, string>; // OpenGraph metadata
  scrollDepth?: number;             // How far user scrolled
  timeOnPage?: number;              // Time spent on page (ms)
  interactionCount?: number;        // Number of interactions
}

export interface PersonNodeData extends BaseNodeData {
  name?: string;                    // Person's name
  email?: string;                   // Email address
  role?: string;                    // Job title or role
  organization?: string;            // Company or org
  avatar?: string;                  // Profile image URL
  socialProfiles?: Record<string, string>; // Social media profiles
}

export interface ProductNodeData extends BaseNodeData {
  name: string;                     // Product name
  price?: number;                   // Price in base currency
  currency?: string;                // Currency code
  brand?: string;                   // Brand name
  category?: string;                // Product category
  rating?: number;                  // User rating
  availability?: string;            // Availability status
  images?: string[];                // Product images
}

export interface ActionNodeData extends BaseNodeData {
  actionType: string;               // Type of action (click, scroll, etc.)
  target?: string;                  // What was acted upon
  value?: any;                      // Action value or data
  duration?: number;                // How long action took
  coordinates?: { x: number; y: number }; // Screen coordinates
}

/**
 * Complete node definition
 */
export interface StateNode {
  // Core identity
  id: NodeId;
  type: NodeType;
  data: BaseNodeData;
  
  // Provenance
  source: DataSource;
  createdAt: Timestamp;
  updatedAt: Timestamp;
  createdBy?: string;               // User or system that created node
  
  // Context
  sessionId?: string;               // Browser session ID
  tabId?: number;                   // Chrome tab ID
  windowId?: number;                // Chrome window ID
  
  // Relationships (for optimization)
  inDegree?: number;                // Number of incoming edges
  outDegree?: number;               // Number of outgoing edges
  
  // Lifecycle
  version?: number;                 // Version for conflict resolution
  deleted?: boolean;                // Soft delete flag
  deletedAt?: Timestamp;            // When node was deleted
}

// ============================================================================
// Edge Definition
// ============================================================================

/**
 * Edge properties and metadata
 */
export interface EdgeData {
  // Core properties
  weight?: number;                  // Edge strength/importance (0-1)
  confidence?: ConfidenceLevel;     // Confidence in relationship
  
  // Metadata
  metadata?: Record<string, any>;   // Flexible metadata
  labels?: string[];                // Edge labels or annotations
  
  // Context
  context?: string;                 // Context where relationship was observed
  evidence?: string[];              // Evidence supporting the relationship
  
  // Temporal
  startTime?: Timestamp;            // When relationship started
  endTime?: Timestamp;              // When relationship ended (if applicable)
  duration?: number;                // Duration of relationship
  
  // Quality
  verified?: boolean;               // Whether relationship is verified
  strength?: number;                // Relationship strength (0-1)
}

/**
 * Complete edge definition
 */
export interface StateEdge {
  // Core identity
  id: EdgeId;
  type: EdgeType;
  
  // Connection
  fromNodeId: NodeId;
  toNodeId: NodeId;
  
  // Properties
  data: EdgeData;
  
  // Provenance
  source: DataSource;
  createdAt: Timestamp;
  updatedAt: Timestamp;
  createdBy?: string;
  
  // Context
  sessionId?: string;
  tabId?: number;
  
  // Lifecycle
  version?: number;
  deleted?: boolean;
  deletedAt?: Timestamp;
}

// ============================================================================
// Graph Query Types
// ============================================================================

/**
 * Query criteria for finding nodes
 */
export interface NodeQuery {
  // Identity filters
  ids?: NodeId[];
  types?: NodeType[];
  
  // Data filters
  titleContains?: string;
  urlMatches?: string | RegExp;
  hasTag?: string;
  hasCategory?: string;
  
  // Provenance filters
  sources?: DataSource[];
  createdAfter?: Timestamp;
  createdBefore?: Timestamp;
  createdBy?: string;
  
  // Context filters
  sessionId?: string;
  tabId?: number;
  
  // Quality filters
  minConfidence?: ConfidenceLevel;
  verified?: boolean;
  
  // Relationship filters
  connectedTo?: NodeId;
  hasIncomingEdge?: EdgeType;
  hasOutgoingEdge?: EdgeType;
  
  // Pagination
  limit?: number;
  offset?: number;
  
  // Sorting
  sortBy?: 'createdAt' | 'updatedAt' | 'title' | 'relevance';
  sortOrder?: 'asc' | 'desc';
}

/**
 * Query criteria for finding edges
 */
export interface EdgeQuery {
  // Identity filters
  ids?: EdgeId[];
  types?: EdgeType[];
  
  // Connection filters
  fromNodeId?: NodeId;
  toNodeId?: NodeId;
  fromNodeTypes?: NodeType[];
  toNodeTypes?: NodeType[];
  
  // Data filters
  minWeight?: number;
  maxWeight?: number;
  minConfidence?: ConfidenceLevel;
  
  // Provenance filters
  sources?: DataSource[];
  createdAfter?: Timestamp;
  createdBefore?: Timestamp;
  
  // Context filters
  sessionId?: string;
  tabId?: number;
  
  // Pagination
  limit?: number;
  offset?: number;
  
  // Sorting
  sortBy?: 'createdAt' | 'weight' | 'confidence';
  sortOrder?: 'asc' | 'desc';
}

/**
 * Traversal options for graph exploration
 */
export interface TraversalOptions {
  // Depth control
  maxDepth?: number;
  minDepth?: number;
  
  // Edge filtering
  edgeTypes?: EdgeType[];
  excludeEdgeTypes?: EdgeType[];
  minEdgeWeight?: number;
  
  // Node filtering
  nodeTypes?: NodeType[];
  excludeNodeTypes?: NodeType[];
  
  // Direction control
  direction?: 'incoming' | 'outgoing' | 'both';
  
  // Termination conditions
  maxNodes?: number;
  stopAtNodeTypes?: NodeType[];
  
  // Algorithm options
  algorithm?: 'bfs' | 'dfs' | 'dijkstra' | 'pagerank';
  
  // Result formatting
  includeEdges?: boolean;
  includeMetadata?: boolean;
}

// ============================================================================
// Graph Statistics and Analytics
// ============================================================================

/**
 * Graph statistics and metrics
 */
export interface GraphStatistics {
  // Size metrics
  nodeCount: number;
  edgeCount: number;
  nodesByType: Record<NodeType, number>;
  edgesByType: Record<EdgeType, number>;
  
  // Quality metrics
  verifiedNodes: number;
  averageConfidence: number;
  
  // Connectivity metrics
  averageDegree: number;
  maxDegree: number;
  isolatedNodes: number;
  connectedComponents: number;
  
  // Temporal metrics
  oldestNode: Timestamp;
  newestNode: Timestamp;
  averageNodeAge: number;
  
  // Data source distribution
  nodesBySource: Record<DataSource, number>;
  edgesBySource: Record<DataSource, number>;
}

/**
 * Node importance metrics
 */
export interface NodeMetrics {
  nodeId: NodeId;
  
  // Centrality measures
  degree: number;
  inDegree: number;
  outDegree: number;
  betweennessCentrality?: number;
  closenessCentrality?: number;
  eigenvectorCentrality?: number;
  pageRank?: number;
  
  // Activity measures
  recentVisits: number;
  totalVisits: number;
  lastVisited?: Timestamp;
  
  // Quality measures
  confidence: ConfidenceLevel;
  verified: boolean;
  
  // User engagement
  timeSpent?: number;
  interactionCount?: number;
  bookmarked?: boolean;
}

// ============================================================================
// Graph Operations Results
// ============================================================================

/**
 * Result of graph query operations
 */
export interface QueryResult<T> {
  data: T[];
  total: number;
  hasMore: boolean;
  cursor?: string;
  executionTime: number;
}

/**
 * Result of graph traversal operations
 */
export interface TraversalResult {
  nodes: StateNode[];
  edges: StateEdge[];
  paths?: Array<{
    nodes: NodeId[];
    edges: EdgeId[];
    weight?: number;
  }>;
  statistics: {
    nodesVisited: number;
    edgesTraversed: number;
    maxDepthReached: number;
    executionTime: number;
  };
}

/**
 * Result of subgraph extraction
 */
export interface SubgraphResult {
  nodes: StateNode[];
  edges: StateEdge[];
  centerNode: NodeId;
  radius: number;
  statistics: GraphStatistics;
}

// ============================================================================
// Graph Events and Updates
// ============================================================================

/**
 * Graph change events for real-time updates
 */
export enum GraphEventType {
  NODE_CREATED = 'node_created',
  NODE_UPDATED = 'node_updated',
  NODE_DELETED = 'node_deleted',
  EDGE_CREATED = 'edge_created',
  EDGE_UPDATED = 'edge_updated',
  EDGE_DELETED = 'edge_deleted',
  GRAPH_CLEARED = 'graph_cleared',
}

export interface GraphEvent {
  type: GraphEventType;
  timestamp: Timestamp;
  nodeId?: NodeId;
  edgeId?: EdgeId;
  data?: any;
  source: DataSource;
  sessionId?: string;
}

/**
 * Batch operation for multiple graph changes
 */
export interface GraphBatch {
  operations: Array<{
    type: 'create_node' | 'update_node' | 'delete_node' | 'create_edge' | 'update_edge' | 'delete_edge';
    data: any;
  }>;
  transactionId?: string;
  source: DataSource;
  timestamp: Timestamp;
}

