/**
 * Magic Button Player V2 - Content SDK
 * 
 * JavaScript SDK for websites to integrate with the Magic Button Player extension.
 * Allows pages to:
 * - Register data with the user state graph
 * - Subscribe to relevant tools and workflows
 * - Provide context for better automation
 * - Receive notifications and updates
 */

import { NodeType, EdgeType, DataSource, BaseNodeData, EdgeData } from '../graph/types';

export interface SDKConfig {
  namespace?: string;
  enableAutoCollection?: boolean;
  enableUserTracking?: boolean;
  apiKey?: string;
  debug?: boolean;
}

export interface SDKNode {
  type: NodeType;
  data: BaseNodeData;
  id?: string;
  metadata?: Record<string, any>;
}

export interface SDKEdge {
  fromNodeId: string;
  toNodeId: string;
  type: EdgeType;
  data?: EdgeData;
}

export interface SDKEvent {
  type: string;
  data: any;
  timestamp: number;
}

export interface ToolContext {
  toolId: string;
  permissions: string[];
  data: any;
}

export interface HandshakeRequest {
  siteName: string;
  siteUrl: string;
  permissions: string[];
  purpose: string;
}

export interface HandshakeResponse {
  success: boolean;
  sessionId?: string;
  code?: string;
  expiresAt?: number;
}

/**
 * Main SDK class for content pages
 */
class MagicButtonSDK {
  private config: SDKConfig;
  private isInitialized = false;
  private isExtensionAvailable = false;
  private messageQueue: any[] = [];
  private eventHandlers = new Map<string, Function[]>();
  private nodeRegistry = new Map<string, SDKNode>();
  private handshakeSessionId: string | null = null;
  private handshakeInProgress = false;
  private accessToken: string | null = null;
  private tokenExpiresAt: number | null = null;
  
  constructor(config: SDKConfig = {}) {
    this.config = {
      namespace: 'magicbutton-sdk',
      enableAutoCollection: true,
      enableUserTracking: false,
      debug: false,
      ...config
    };
    
    this.log('SDK created with config:', this.config);
  }

  /**
   * Initialize the SDK and establish communication with extension
   */
  async initialize(): Promise<boolean> {
    if (this.isInitialized) {
      this.log('SDK already initialized');
      return this.isExtensionAvailable;
    }

    this.log('Initializing SDK...');

    try {
      // Check if extension is available
      this.isExtensionAvailable = await this.detectExtension();
      
      if (this.isExtensionAvailable) {
        // Set up communication bridge
        this.setupMessageBridge();
        
        // Register page with extension
        await this.registerPage();
        
        // Enable auto-collection if configured
        if (this.config.enableAutoCollection) {
          this.enableAutoCollection();
        }
        
        // Process queued messages
        await this.processMessageQueue();
        
        this.log('SDK initialized successfully');
      } else {
        this.log('Extension not detected - SDK running in standalone mode');
      }
      
      this.isInitialized = true;
      this.emit('sdk:initialized', { available: this.isExtensionAvailable });
      
      return this.isExtensionAvailable;
    } catch (error) {
      this.error('Failed to initialize SDK:', error);
      return false;
    }
  }

  /**
   * Detect if Magic Button Player extension is available
   */
  private async detectExtension(): Promise<boolean> {
    return new Promise((resolve) => {
      // Send detection message
      window.postMessage({
        type: 'MAGICBUTTON_SDK_DETECTION',
        timestamp: Date.now()
      }, window.location.origin);
      
      // Listen for response
      const listener = (event: MessageEvent) => {
        if (event.source === window && 
            event.data.type === 'MAGICBUTTON_EXTENSION_RESPONSE') {
          window.removeEventListener('message', listener);
          resolve(true);
        }
      };
      
      window.addEventListener('message', listener);
      
      // Timeout after 1 second
      setTimeout(() => {
        window.removeEventListener('message', listener);
        resolve(false);
      }, 1000);
    });
  }

  /**
   * Set up message bridge with extension
   */
  private setupMessageBridge(): void {
    window.addEventListener('message', (event) => {
      if (event.source !== window) return;
      
      if (event.data.type === 'MAGICBUTTON_SDK_RESPONSE') {
        this.handleExtensionMessage(event.data);
      }
    });
  }

  /**
   * Register current page with extension
   */
  private async registerPage(): Promise<void> {
    const pageData = this.collectPageData();
    
    await this.sendMessage({
      type: 'REGISTER_PAGE',
      data: {
        ...pageData,
        sdkVersion: '2.0.0',
        config: this.config
      }
    });
  }

  /**
   * Collect basic page data
   */
  private collectPageData(): any {
    return {
      url: window.location.href,
      title: document.title,
      timestamp: Date.now(),
      userAgent: navigator.userAgent,
      referrer: document.referrer,
      // OpenGraph data
      openGraph: this.extractOpenGraphData(),
      // Schema.org data
      structuredData: this.extractStructuredData()
    };
  }

  /**
   * Extract OpenGraph metadata
   */
  private extractOpenGraphData(): Record<string, string> {
    const ogData: Record<string, string> = {};
    const ogTags = document.querySelectorAll('meta[property^="og:"]');
    
    ogTags.forEach(tag => {
      const property = tag.getAttribute('property')?.replace('og:', '');
      const content = tag.getAttribute('content');
      if (property && content) {
        ogData[property] = content;
      }
    });
    
    return ogData;
  }

  /**
   * Extract structured data (JSON-LD)
   */
  private extractStructuredData(): any[] {
    const scripts = document.querySelectorAll('script[type="application/ld+json"]');
    const structuredData: any[] = [];
    
    scripts.forEach(script => {
      try {
        const data = JSON.parse(script.textContent || '');
        structuredData.push(data);
      } catch (error) {
        this.log('Failed to parse structured data:', error);
      }
    });
    
    return structuredData;
  }

  // ============================================================================
  // Public API Methods
  // ============================================================================

  /**
   * Add a node to the user state graph
   */
  async addNode(node: SDKNode): Promise<string> {
    const nodeId = node.id || this.generateNodeId();
    
    // Store in local registry
    this.nodeRegistry.set(nodeId, { ...node, id: nodeId });
    
    // Send to extension if available
    if (this.isExtensionAvailable) {
      await this.sendMessage({
        type: 'ADD_NODE',
        data: {
          id: nodeId,
          ...node,
          source: DataSource.SDK,
          timestamp: Date.now()
        }
      });
    }
    
    this.emit('node:added', { nodeId, node });
    return nodeId;
  }

  /**
   * Add an edge between nodes
   */
  async addEdge(edge: SDKEdge): Promise<string> {
    const edgeId = this.generateEdgeId();
    
    if (this.isExtensionAvailable) {
      await this.sendMessage({
        type: 'ADD_EDGE',
        data: {
          id: edgeId,
          ...edge,
          source: DataSource.SDK,
          timestamp: Date.now()
        }
      });
    }
    
    this.emit('edge:added', { edgeId, edge });
    return edgeId;
  }

  /**
   * Track user action/event
   */
  async trackAction(actionType: string, data: any): Promise<void> {
    const actionData = {
      type: actionType,
      data,
      timestamp: Date.now(),
      url: window.location.href,
      userAgent: navigator.userAgent
    };
    
    if (this.isExtensionAvailable) {
      await this.sendMessage({
        type: 'TRACK_ACTION',
        data: actionData
      });
    }
    
    this.emit('action:tracked', actionData);
  }

  /**
   * Register for tool notifications
   */
  async registerForTools(toolTypes: string[]): Promise<void> {
    if (this.isExtensionAvailable) {
      await this.sendMessage({
        type: 'REGISTER_TOOLS',
        data: { toolTypes }
      });
    }
    
    this.emit('tools:registered', { toolTypes });
  }

  /**
   * Request user consent for data collection
   */
  async requestConsent(dataTypes: string[], purpose: string): Promise<boolean> {
    if (!this.isExtensionAvailable) {
      return false;
    }
    
    const response = await this.sendMessage({
      type: 'REQUEST_CONSENT',
      data: { dataTypes, purpose }
    });
    
    const granted = response?.granted || false;
    this.emit('consent:response', { granted, dataTypes, purpose });
    
    return granted;
  }

  /**
   * Get current user context from extension
   */
  async getUserContext(): Promise<any> {
    if (!this.isExtensionAvailable) {
      return null;
    }
    
    const response = await this.sendMessage({
      type: 'GET_USER_CONTEXT'
    });
    
    return response?.context || null;
  }

  /**
   * Subscribe to tool activation events
   */
  onToolActivated(callback: (tool: ToolContext) => void): void {
    this.on('tool:activated', callback);
  }

  /**
   * Subscribe to graph updates
   */
  onGraphUpdated(callback: (update: any) => void): void {
    this.on('graph:updated', callback);
  }

  // ============================================================================
  // Handshake Methods
  // ============================================================================

  /**
   * Initiate handshake with Magic Button extension
   * This will cause the extension sidepanel to flash and display a 6-digit code
   */
  async initiateHandshake(request: HandshakeRequest): Promise<HandshakeResponse> {
    this.log('ü§ù Initiating handshake with request:', request);
    
    if (!this.isExtensionAvailable) {
      this.error('‚ùå Extension not available for handshake');
      throw new Error('Magic Button extension not available');
    }

    if (this.handshakeInProgress) {
      this.warn('‚ö†Ô∏è Handshake already in progress');
      throw new Error('Handshake already in progress');
    }

    this.handshakeInProgress = true;

    try {
      this.log('üì§ Sending handshake message to extension...');

      const message = {
        type: 'INITIATE_HANDSHAKE',
        data: {
          siteName: request.siteName,
          siteUrl: request.siteUrl,
          permissions: request.permissions,
          purpose: request.purpose,
          timestamp: Date.now()
        }
      };
      
      this.log('üìã Message payload:', message);

      const response = await this.sendMessage(message);
      
      this.log('üì• Received handshake response:', response);

      if (response?.success) {
        this.handshakeSessionId = response.sessionId;
        this.log('‚úÖ Handshake initiated successfully, session ID:', response.sessionId);
        this.emit('handshake:initiated', response);
        return response;
      } else {
        this.error('‚ùå Handshake initiation failed:', response?.error);
        throw new Error(response?.error || 'Handshake initiation failed');
      }
    } catch (error) {
      this.error('üí• Handshake error:', error);
      throw error;
    } finally {
      this.handshakeInProgress = false;
    }
  }

  /**
   * Complete handshake by submitting the 6-digit code from extension
   */
  async submitHandshakeCode(code: string): Promise<boolean> {
    if (!this.isExtensionAvailable) {
      throw new Error('Magic Button extension not available');
    }

    if (!this.handshakeSessionId) {
      throw new Error('No active handshake session. Call initiateHandshake() first.');
    }

    // Validate code format (6 digits)
    if (!/^\d{6}$/.test(code)) {
      throw new Error('Code must be exactly 6 digits');
    }

    this.log('Submitting handshake code:', code);

    try {
      const response = await this.sendMessage({
        type: 'SUBMIT_HANDSHAKE_CODE',
        data: {
          sessionId: this.handshakeSessionId,
          code: code,
          timestamp: Date.now()
        }
      });

      if (response?.success) {
        // Store the JWT token
        this.accessToken = response.token;
        this.tokenExpiresAt = response.expiresAt;
        
        this.emit('handshake:completed', { 
          sessionId: this.handshakeSessionId,
          token: response.token,
          permissions: response.permissions,
          expiresAt: response.expiresAt
        });
        this.log('Handshake completed successfully');
        return true;
      } else {
        this.emit('handshake:failed', { 
          sessionId: this.handshakeSessionId, 
          error: response?.error 
        });
        throw new Error(response?.error || 'Invalid handshake code');
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Unknown error';
      this.emit('handshake:failed', { 
        sessionId: this.handshakeSessionId, 
        error: errorMessage 
      });
      throw error;
    }
  }

  /**
   * Cancel active handshake session
   */
  async cancelHandshake(): Promise<void> {
    if (!this.handshakeSessionId) {
      return;
    }

    if (this.isExtensionAvailable) {
      await this.sendMessage({
        type: 'CANCEL_HANDSHAKE',
        data: {
          sessionId: this.handshakeSessionId,
          timestamp: Date.now()
        }
      });
    }

    this.emit('handshake:cancelled', { sessionId: this.handshakeSessionId });
    this.handshakeSessionId = null;
    this.handshakeInProgress = false;
  }

  /**
   * Check if handshake is required for the current site
   */
  async checkHandshakeStatus(): Promise<{ required: boolean; sessionId?: string }> {
    if (!this.isExtensionAvailable) {
      return { required: false };
    }

    const response = await this.sendMessage({
      type: 'CHECK_HANDSHAKE_STATUS',
      data: {
        siteUrl: window.location.href,
        timestamp: Date.now()
      }
    });

    return {
      required: response?.required || false,
      sessionId: response?.sessionId
    };
  }

  /**
   * Get current handshake session ID
   */
  getHandshakeSessionId(): string | null {
    return this.handshakeSessionId;
  }

  /**
   * Check if handshake is in progress
   */
  isHandshakeInProgress(): boolean {
    return this.handshakeInProgress;
  }

  /**
   * Open Magic Button sidepanel
   */
  async openSidepanel(): Promise<boolean> {
    if (!this.isExtensionAvailable) {
      throw new Error('Magic Button extension not available');
    }

    try {
      this.log('Opening sidepanel');
      
      const response = await this.sendMessage({
        type: 'OPEN_SIDEPANEL'
      });

      if (response?.success) {
        this.emit('sidepanel:opened', { timestamp: Date.now() });
        return true;
      } else {
        throw new Error(response?.error || 'Failed to open sidepanel');
      }
    } catch (error) {
      this.error('Failed to open sidepanel:', error);
      throw error;
    }
  }

  /**
   * Get current access token
   */
  getAccessToken(): string | null {
    // Check if token is expired
    if (this.tokenExpiresAt && Date.now() > this.tokenExpiresAt) {
      this.accessToken = null;
      this.tokenExpiresAt = null;
      this.emit('token:expired', { timestamp: Date.now() });
      return null;
    }
    
    return this.accessToken;
  }

  /**
   * Check if SDK is authenticated
   */
  isAuthenticated(): boolean {
    return this.getAccessToken() !== null;
  }

  /**
   * Clear authentication token
   */
  clearAuthentication(): void {
    this.accessToken = null;
    this.tokenExpiresAt = null;
    this.emit('authentication:cleared', { timestamp: Date.now() });
  }

  // ============================================================================
  // Auto-collection Features
  // ============================================================================

  /**
   * Enable automatic data collection
   */
  private enableAutoCollection(): void {
    // Track page navigation
    this.trackPageNavigation();
    
    // Track user interactions
    if (this.config.enableUserTracking) {
      this.trackUserInteractions();
    }
    
    // Track form submissions
    this.trackFormSubmissions();
    
    // Monitor DOM changes
    this.monitorDOMChanges();
  }

  /**
   * Track page navigation events
   */
  private trackPageNavigation(): void {
    // Track initial page load
    this.trackAction('page_loaded', this.collectPageData());
    
    // Track page unload
    window.addEventListener('beforeunload', () => {
      this.trackAction('page_unloaded', {
        url: window.location.href,
        timeOnPage: Date.now() - (window as any).__magicButtonPageLoadTime
      });
    });
    
    // Track history changes (SPA navigation)
    const originalPushState = history.pushState;
    const originalReplaceState = history.replaceState;
    
    history.pushState = (...args) => {
      originalPushState.apply(history, args);
      this.trackAction('navigation', { 
        type: 'pushState', 
        url: window.location.href 
      });
    };
    
    history.replaceState = (...args) => {
      originalReplaceState.apply(history, args);
      this.trackAction('navigation', { 
        type: 'replaceState', 
        url: window.location.href 
      });
    };
    
    window.addEventListener('popstate', () => {
      this.trackAction('navigation', { 
        type: 'popstate', 
        url: window.location.href 
      });
    });
  }

  /**
   * Track user interactions
   */
  private trackUserInteractions(): void {
    // Track clicks
    document.addEventListener('click', (event) => {
      const target = event.target as Element;
      this.trackAction('click', {
        tagName: target.tagName,
        className: target.className,
        id: target.id,
        href: target.getAttribute('href'),
        x: event.clientX,
        y: event.clientY
      });
    });
    
    // Track scroll
    let scrollTimeout: number;
    window.addEventListener('scroll', () => {
      clearTimeout(scrollTimeout);
      scrollTimeout = window.setTimeout(() => {
        this.trackAction('scroll', {
          scrollY: window.scrollY,
          scrollX: window.scrollX,
          documentHeight: document.documentElement.scrollHeight,
          viewportHeight: window.innerHeight
        });
      }, 500);
    });
  }

  /**
   * Track form submissions
   */
  private trackFormSubmissions(): void {
    document.addEventListener('submit', (event) => {
      const form = event.target as HTMLFormElement;
      const formData = new FormData(form);
      const data: Record<string, any> = {};
      
      // Only collect non-sensitive data
      for (const [key, value] of formData.entries()) {
        if (!this.isSensitiveField(key)) {
          data[key] = value;
        }
      }
      
      // Count form fields
      const fieldCount = Array.from(formData.entries()).length;
      
      this.trackAction('form_submitted', {
        action: form.action,
        method: form.method,
        fieldCount: fieldCount,
        hasFileUpload: Array.from(form.elements).some(el => 
          el instanceof HTMLInputElement && el.type === 'file'
        )
      });
    });
  }

  /**
   * Monitor DOM changes for dynamic content
   */
  private monitorDOMChanges(): void {
    const observer = new MutationObserver((mutations) => {
      let hasSignificantChanges = false;
      
      mutations.forEach(mutation => {
        if (mutation.type === 'childList' && mutation.addedNodes.length > 0) {
          hasSignificantChanges = true;
        }
      });
      
      if (hasSignificantChanges) {
        this.trackAction('dom_updated', {
          mutationCount: mutations.length,
          timestamp: Date.now()
        });
      }
    });
    
    observer.observe(document.body, {
      childList: true,
      subtree: true
    });
  }

  /**
   * Check if a field name indicates sensitive data
   */
  private isSensitiveField(fieldName: string): boolean {
    const sensitivePatterns = [
      /password/i,
      /ssn|social/i,
      /credit|card/i,
      /cvv|cvc/i,
      /bank/i,
      /secret/i,
      /token/i,
      /api[_-]?key/i
    ];
    
    return sensitivePatterns.some(pattern => pattern.test(fieldName));
  }

  // ============================================================================
  // Utility Methods
  // ============================================================================

  /**
   * Send message to extension
   */
  private async sendMessage(message: any): Promise<any> {
    return new Promise((resolve, reject) => {
      if (!this.isExtensionAvailable) {
        this.warn('‚ö†Ô∏è Extension not available, queueing message:', message.type);
        this.messageQueue.push(message);
        resolve(null);
        return;
      }
      
      const messageId = this.generateMessageId();
      this.log('üì§ Sending message to extension:', { messageId, type: message.type });
      
      const timeout = setTimeout(() => {
        this.error('‚è∞ Message timeout for:', { messageId, type: message.type });
        reject(new Error(`Message timeout for ${message.type}`));
      }, 5000);
      
      const listener = (event: MessageEvent) => {
        if (event.source === window && 
            event.data.type === 'MAGICBUTTON_SDK_RESPONSE' &&
            event.data.messageId === messageId) {
          
          this.log('üì• Received response for message:', { messageId, type: message.type });
          clearTimeout(timeout);
          window.removeEventListener('message', listener);
          
          if (event.data.error) {
            this.error('‚ùå Extension returned error:', event.data.error);
            reject(new Error(event.data.error));
          } else {
            resolve(event.data.response);
          }
        }
      };
      
      window.addEventListener('message', listener);
      
      // Include token in message if available
      const messageWithAuth = {
        ...message,
        token: this.getAccessToken()
      };

      const postMessagePayload = {
        type: 'MAGICBUTTON_SDK_MESSAGE',
        messageId,
        payload: messageWithAuth
      };
      
      this.log('üì® PostMessage payload:', postMessagePayload);
      
      window.postMessage(postMessagePayload, window.location.origin);
    });
  }

  /**
   * Process queued messages
   */
  private async processMessageQueue(): Promise<void> {
    while (this.messageQueue.length > 0) {
      const message = this.messageQueue.shift();
      try {
        await this.sendMessage(message);
      } catch (error) {
        this.error('Failed to send queued message:', error);
      }
    }
  }

  /**
   * Handle messages from extension
   */
  private handleExtensionMessage(data: any): void {
    switch (data.type) {
      case 'TOOL_ACTIVATED':
        this.emit('tool:activated', data.tool);
        break;
      case 'GRAPH_UPDATED':
        this.emit('graph:updated', data.update);
        break;
      case 'CONSENT_REQUEST':
        this.handleConsentRequest(data);
        break;
      case 'HANDSHAKE_CODE_GENERATED':
        this.emit('handshake:code-generated', data);
        break;
      case 'HANDSHAKE_EXPIRED':
        this.emit('handshake:expired', data);
        this.handshakeSessionId = null;
        this.handshakeInProgress = false;
        break;
      case 'HANDSHAKE_SIDEPANEL_FLASH':
        this.emit('handshake:sidepanel-flash', data);
        break;
      default:
        this.log('Unknown message from extension:', data);
    }
  }

  /**
   * Handle consent request from extension
   */
  private handleConsentRequest(data: any): void {
    // This could show a custom consent UI
    const granted = confirm(`Allow Magic Button Player to access ${data.dataTypes.join(', ')} for ${data.purpose}?`);
    
    window.postMessage({
      type: 'MAGICBUTTON_SDK_CONSENT_RESPONSE',
      requestId: data.requestId,
      granted
    }, window.location.origin);
  }

  // ============================================================================
  // Event System
  // ============================================================================

  /**
   * Subscribe to SDK events
   */
  on(eventType: string, handler: Function): void {
    if (!this.eventHandlers.has(eventType)) {
      this.eventHandlers.set(eventType, []);
    }
    this.eventHandlers.get(eventType)!.push(handler);
  }

  /**
   * Unsubscribe from SDK events
   */
  off(eventType: string, handler?: Function): void {
    if (!this.eventHandlers.has(eventType)) return;
    
    if (handler) {
      const handlers = this.eventHandlers.get(eventType)!;
      const index = handlers.indexOf(handler);
      if (index > -1) {
        handlers.splice(index, 1);
      }
    } else {
      this.eventHandlers.delete(eventType);
    }
  }

  /**
   * Emit SDK event
   */
  private emit(eventType: string, data?: any): void {
    const handlers = this.eventHandlers.get(eventType) || [];
    handlers.forEach(handler => {
      try {
        handler(data);
      } catch (error) {
        this.error('Event handler error:', error);
      }
    });
  }

  // ============================================================================
  // ID Generation
  // ============================================================================

  private generateNodeId(): string {
    return `node_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  }

  private generateEdgeId(): string {
    return `edge_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  }

  private generateMessageId(): string {
    return `msg_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  }

  // ============================================================================
  // Logging
  // ============================================================================

  private log(...args: any[]): void {
    if (this.config.debug) {
      console.log('[MagicButton SDK]', ...args);
    }
  }

  private error(...args: any[]): void {
    console.error('[MagicButton SDK]', ...args);
  }
}

// ============================================================================
// Global SDK Instance
// ============================================================================

declare global {
  interface Window {
    MagicButton?: MagicButtonSDK;
    __MagicButtonSDK?: MagicButtonSDK;
  }
}

/**
 * Auto-initialize SDK if window.MagicButtonConfig is present
 */
if (typeof window !== 'undefined') {
  // Mark page load time for tracking
  (window as any).__magicButtonPageLoadTime = Date.now();
  
  // Check for global config
  const globalConfig = (window as any).MagicButtonConfig;
  if (globalConfig || (window as any).MagicButtonAutoInit) {
    const sdk = new MagicButtonSDK(globalConfig);
    window.MagicButton = sdk;
    window.__MagicButtonSDK = sdk;
    
    // Auto-initialize
    sdk.initialize().then(success => {
      if (success) {
        console.log('[MagicButton] SDK initialized successfully');
      }
    });
  }
}

export default MagicButtonSDK;
export { MagicButtonSDK };